@mixin basic-font($text-rendering: true) {
  font-family: $basic-sans-serif;
  @if $text-rendering {
    text-rendering: optimizelegibility; } }

@mixin sans-serif($text-rendering: true) {
  font-family: $basic-sans-serif;
  @if $text-rendering {
    text-rendering: optimizelegibility; } }

@mixin maru-serif($text-rendering: true) {
  font-family: $basic-maru;
  @if $text-rendering {
    text-rendering: optimizelegibility; } }

@mixin bold-ja($text-rendering: true) {
  font-family: $basic-bold-sans-serif;
  font-weight: $basic-bold-font-weight;
  @if $text-rendering {
    text-rendering: optimizelegibility; } }

@mixin serif($text-rendering: true) {
  font-family: $basic-serif;
  @if $text-rendering {
    text-rendering: optimizelegibility; } }



