.previous-next-nav {
  margin-top: 4rem;
  @include media-breakpoint-down(sm) {
    margin-top: 2rem; } }

.previous-next-nav__items {
  display: flex;
  justify-content: space-between; }

.previous-next-nav__item {
  max-width: 50%;
  &.is-previous {
    text-align: left; }
  &.is-next {
    text-align: right; } }

.previous-next-nav__link {
  @include block-link;
  @include text-block(.875rem 1.5rem, $text-color);
  @include hover-link;
  &:hover {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    background-color: $text-color;
    @include text-block(1rem 1, $base-color center flex);
    align-items: center;
    justify-content: center;
    @include size(2.5rem); } }

.previous-next-nav__icon {
  font-size: .875rem;
  &.is-previous {
    margin-right: .5rem;
    @include media-breakpoint-down(sm) {
      margin: 0; } }
  &.is-next {
    margin-left: .5rem;
    @include media-breakpoint-down(sm) {
      margin: 0; } } }

.previous-next-nav__label {
  display: none;
  @include text-block(.75rem 1, center);
  width: 2rem;
  padding-top: .375rem;
  @include media-breakpoint-down(sm) {
    display: block;
    width: 100%;
    font-size: .625rem; } }



