.commissioned-development-features {
  margin-bottom: 2rem; }

.commissioned-development-features__items {
  display: flex;
  align-items: center;
  justify-content: center; }

.commissioned-development-features-item {
  @include margin(horizontal, 2rem); }

.commissioned-development-features-item__link {
  transition: all .4s ease-in; }

.commissioned-development-features-item__image {
  @include size(8rem);
  border-radius: 50%;
  border: solid .125rem $default-text;
  padding: .25rem;
  margin-bottom: .75rem;
  &:nth-child(2) {
    @include margin(horizontal, 2.5rem); }
  .commissioned-development-features-item__link:hover & {
    border-color: #00b2e6;
    opacity: .8; } }

.commissioned-development-features-item__label {
  @include text-block(.875rem 1.4, center $default-text);
  .commissioned-development-features-item__link:hover & {
    text-decoration: underline; } }



