.article-tags__item {
  display: inline-block;
  margin-right: .5rem;
  @include media-breakpoint-down(sm) {
    @include inline-block(bottom);
    margin-right: .5rem; } }

.article-tags__link {
  @include inline-block(bottom);
  @include position(relative);
  @include text-block(.875rem 1.6 .75rem, nowrap);
  text-decoration: none;
  color: $text-color;
  @include media-breakpoint-down(sm) {
    margin-bottom: .5rem; }
  &:hover {
    color: $accent-color; }
  @include media-breakpoint-down(sm) {
    @include text-block(.75rem 1.6); }
  &:before {
    @include fa('\f02b');
    margin-right: .375rem;
    line-height: 1.5; } }



