.is-long-text {
  >*:last-child {
    margin-bottom: 0; }
  .highlight {
    @include source-code-pro; }
  p {
    @include text-block(1rem 1.8 1.2em);
    @include media-breakpoint-down(sm) {
      @include text-block(.8125rem 1.7 1.2em); }
    a {
      word-break: break-all; } }
  a {
    //color: desaturate($accent-color, 32%)
    @include hover-link-reversal;
    &:hover {
      img {
        box-shadow: color_select($base-color, $main-color, $accent-color) 0 0 0 2px; } } }
  code {
    @include source-code-pro;
    display: inline-block;
    @include padding(horizontal, .375rem);
    @include text-block(.8125rem 1.25rem);
    letter-spacing: 0;
    color: $text-color; }
  hr {
    &:before {
      @include fa('\f069\f069\f069');
      display: block;
      @include text-block(.8125rem 14px, center);
      @include margin(vertical, 36px);
      @include letter-spacing-center(4px);
      @include media-breakpoint-down(sm) {
        @include text-block(.75rem 14px, center);
        @include margin(vertical, 1.5rem); } } }
  img {
    max-width: 100%;
    display: block;
    @include margin(horizontal, auto);
    border: solid .0625rem rgba($text-color, .1);
    @include media-breakpoint-down(sm) {
      max-width: 100% !important; }
    &.is-xs {
      max-width: 10rem; }
    &.is-sm {
      max-width: 20rem; }
    &.is-md {
      max-width: 30rem; } }

  blockquote {
    padding-left: 2rem;
    @include margin(vertical, 1.5rem 2.5rem);
    @include position(relative);
    border-left: solid .25rem $base-color;
    font-style: italic;
    @include media-breakpoint-down(sm) {
      padding: .75rem 1rem .75rem 2rem;
      @include margin(vertical, 38px 30px); }
    p + & {
      @include margin(vertical, 3rem 2.5rem); }
    p {
      line-height: 2.15; }
    code {
      font-style: normal; }
    >ul {}
    >ol {}
    >*:last-child {
      margin-bottom: 0 !important; } }
  h2 {
    @include text-block(1.625rem 1.4 1.25rem, bold);
    @include bold-ja;
    letter-spacing: .5px;
    padding-bottom: .5rem;
    border-bottom: 2px solid $text-color;
    @include media-breakpoint-down(sm) {
      @include text-block(1.25rem 1.3, bold); } }
  * + h2 {
    margin-top: 1.5rem; }
  h3 {
    @include text-block(1.375rem 1.4 1.125rem, bold);
    @include bold-ja;
    @include media-breakpoint-down(sm) {
      @include text-block(1.125rem 1.3, bold); } }
  h4 {
    @include text-block(1.125rem 1.3 1.25rem, bold);
    @include bold-ja;
    @include media-breakpoint-down(sm) {
      @include text-block(1rem 1.3, bold); } }
  >ul {
    list-style-type: disc; }
  >ol {
    list-style-type: decimal; }
  li {
    ul,
    ol {
      margin-top: 1em; }
    >ul {
      list-style-type: circle;
      >li {
        >ul {
          list-style-type: square; } } } }
  ul,
  ol {
    list-style-position: outside;
    margin-left: 1.75em;
    margin-bottom: 1.4em; }
  li {
    @include text-block(1rem 1.8 .5em);
    &:last-child {
      margin-bottom: 0; } }
  p + ul,
  p + ol {
    margin-top: 1rem; }

  dl {
    margin-bottom: 2rem;
    @include border(vertical, 1px solid $text-color);
    @include padding(vertical, 2rem);
    @extend .row;
    @include margin(horizontal, 0);
    @include media-breakpoint-down(sm) {
      @include padding(vertical, 1.5rem);
      @include border(vertical, 1px dotted $text-color); } }
  dt {
    @include text-block(.8125rem 1.6 .5rem, bold);
    margin-top: 1rem;
    padding-right: .5rem;
    @extend .col-sm-4;
    @extend .col-xs-12;
    @include media-breakpoint-down(sm) {
      padding: 0; }
    &:first-child {
      margin-top: 0;
      &+dd {
        margin-top: 0; } }
    &+dd {
      margin-top: 1rem;
      @include media-breakpoint-down(sm) {
        margin-top: 0; } } }
  dd {
    @include text-block(.8125rem 1.6 1em);
    @extend .col-sm-8;
    @extend .col-xs-12;
    @include media-breakpoint-down(sm) {
      @include text-block(.75rem 1.6 1em);
      padding: 0; }
    &+dd {
      @extend .offset-sm-4; }
    &:last-child {
      margin-bottom: 0; } }
  label {
    @include text-block(1rem 1.5 .2rem, bold block); }
  textarea,
  input[type="text"] {
    display: block;
    border: 1px solid $text-color;
    width: 100%;
    margin-bottom: 1.5rem;
    padding: .5rem;
    @include text-block(1rem 1.8 $text-color); }
  input[type="submit"] {
    border: 1px solid $text-color;
    padding: .5rem 1rem;
    margin-bottom: 1.5rem; }
  iframe {
    max-width: 100%;
    margin-bottom: 2rem;
    display: block;
    @include margin(horizontal, auto); }
  .amazlet-box:last-child {
    margin-top: 4rem;
    .amazlet-image img {
      width: 10rem; } } }



